/* The following line can be included in a src/App.scss */
@import "~bootstrap/scss/bootstrap";
@import "icomoon.scss";

$font-primary: "Poppins", Arial, sans-serif;

$white: #fff;
$black: #000000;
$darken: #232931;

$primary: #990234;
$secondary: #a0f669;

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  -ms-border-radius: $radius;
  border-radius: $radius;
}

@mixin transition($transition) {
  -moz-transition: all $transition ease;
  -o-transition: all $transition ease;
  -webkit-transition: all $transition ease;
  -ms-transition: all $transition ease;
  transition: all $transition ease;
}

html {
  // overflow-x: hidden;
}
body {
  font-family: $font-primary;
  background: $black;
  font-size: 16px;
  line-height: 1.8;
  font-weight: 400;
  color: lighten($black, 60%);
  &.menu-show {
    overflow: hidden;
    position: fixed;
    height: 100%;
    width: 100%;
  }
}
a {
  color: $primary;
  text-decoration: none;
  transition: 0.5s all ease;
  &:hover,
  &:focus {
    text-decoration: underline;
    color: $primary;
    outline: none !important;
  }
}
h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5 {
  line-height: 1.5;
  color: rgba(255, 255, 255, 1);
  font-weight: 400;
}

.text-primary {
  color: $primary !important;
}

.portfolio-navbar-light {
  background: transparent !important;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  // padding-top: 30px;
  @media (max-width: 991.98px) {
    background: $black !important;
    position: relative;
    top: 0;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 15px;
    padding-right: 15px;
  }
  .container {
    width: 1110px;
    margin: 0 auto;
    padding: 0;
    @include media-breakpoint-up(lg) {
    }
  }

  .navbar-brand {
    color: $white;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    &:hover,
    &:focus {
      color: $white !important;
    }
    @media (max-width: 991.98px) {
      color: $white;
    }
  }

  .navbar-nav {
    @media (max-width: 991.98px) {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      padding-bottom: 20px;
      margin-right: 0;
    }
    > .nav-item {
      > .nav-link {
        font-size: 16px;
        padding-top: 0.7rem;
        padding-bottom: 0.7rem;
        padding-left: 20px;
        padding-right: 20px;
        color: $white;
        font-weight: 400;
        opacity: 1 !important;
        span {
          position: relative;
          display: block;
          padding-bottom: 2px;
          @include media-breakpoint-down(lg) {
            display: inline-block;
          }
          &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            bottom: 0;
            left: 0;
            background: $primary;
            visibility: visible;
            -webkit-transform: scaleX(0);
            -moz-transform: scaleX(0);
            -ms-transform: scaleX(0);
            -o-transform: scaleX(0);
            transform: scaleX(0);
            -webkit-transition: all 0.3s ease-in-out 0s;
            -moz-transition: all 0.3s ease-in-out 0s;
            -ms-transition: all 0.3s ease-in-out 0s;
            -o-transition: all 0.3s ease-in-out 0s;
            transition: all 0.3s ease-in-out 0s;
          }
        }
        &:hover {
          text-decoration: none;
          span {
            &:before {
              visibility: visible;
              -webkit-transform: scaleX(1);
              -moz-transform: scaleX(1);
              -ms-transform: scaleX(1);
              -o-transform: scaleX(1);
              transform: scaleX(1);
            }
          }
        }
        @media (max-width: 991.98px) {
          padding-left: 0;
          padding-right: 0;
          padding-bottom: 1rem;
          padding-top: 0;
          color: $white;
        }
      }

      &.cta {
        > a {
          color: $white;
          border: 1px solid $primary;
          padding-top: 0.7rem;
          padding-bottom: 0.7rem;
          padding-left: 18px;
          padding-right: 18px;
          background: $primary;
          margin-top: 0;
          @include border-radius(5px);
          span {
            display: inline-block;
            color: $white;
          }
          &:hover {
            background: $primary;
            border: 1px solid $primary;
          }
        }
        &.cta-colored {
          a {
            border: 1px solid $secondary;
            background: $secondary !important;
          }
        }
      }

      .nav-link {
        &.active {
          background: transparent;
          background: none;
          color: $primary;
          span {
            &:before {
              visibility: visible;
              -webkit-transform: scaleX(1);
              transform: scaleX(1);
            }
          }
        }
      }

      @media (max-width: 991.98px) {
        &.active {
          > a {
            color: $primary;
          }
        }
      }
    }
  }
  .navbar-toggler {
    border: none;
    color: rgba(255, 255, 255, 0.5) !important;
    cursor: pointer;
    padding-right: 0;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 0.1em;
    &:hover,
    &:focus {
      text-decoration: none;
      color: $primary;
      outline: none !important;
    }
  }

  &.scrolled {
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    margin-top: -130px;
    background: $black !important;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
    padding: 0 15px;
    .nav-item {
      > .nav-link {
        color: $white !important;
        @media (max-width: 991.98px) {
          padding-left: 0 !important;
          padding-right: 0 !important;
          padding-bottom: 20px !important;
        }
      }
      &.active {
        > a {
          color: $primary !important;
        }
      }
      &.cta {
        > a {
          color: $white !important;
          background: $primary;
          border: none !important;
          padding-top: 0.5rem !important;
          padding-bottom: 0.5rem !important;
          padding-left: 20px !important;
          padding-right: 20px !important;
          margin-top: 5px !important;
          @include border-radius(5px);
          span {
            display: inline-block;
            color: $white !important;
          }
        }
        &.cta-colored {
          span {
            border-color: $primary;
          }
        }
      }
    }

    .navbar-nav {
      @media (max-width: 991.98px) {
        background: none;
        border-radius: 0px;
        padding-left: 0 !important;
        padding-right: 0 !important;
        margin-left: 0 !important;
      }
    }

    .navbar-toggler {
      border: none;
      color: rgba(0, 0, 0, 0.5) !important;
      border-color: rgba(0, 0, 0, 0.5) !important;
      cursor: pointer;
      text-transform: uppercase;
      font-size: 16px;
      letter-spacing: 0.1em;
    }
    .nav-link {
      color: $black !important;
      &.active {
        color: $primary !important;
      }
    }
    &.awake {
      margin-top: 0px;
      transition: 0.3s all ease-out;
    }
    &.sleep {
      transition: 0.3s all ease-out;
    }

    .navbar-brand {
      color: $white;
      padding-top: 1rem;
      padding-bottom: 1rem;
      font-size: 24px;
    }
  }
}

.navbar-brand {
  font-weight: 900;
  font-size: 24px;
  text-transform: uppercase;
}

.hero {
  h3.vr {
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    text-orientation: sideways;
    text-align: center;
    text-orientation: sideways;
    margin: 0rem;
    position: absolute;
    top: 0;
    left: 4em;
    height: 100%;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 5px;
    font-size: 16px;
    z-index: 1;
    color: rgba(0, 0, 0, 0.3);
    @include media-breakpoint-down(lg) {
      opacity: 0;
      display: none;
    }
  }
}

//OWL CAROUSEL
.owl-carousel {
  position: relative;
  .owl-item {
    opacity: 0.4;
    &.active {
      opacity: 1;
    }
  }
  .owl-dots {
    text-align: center;
    .owl-dot {
      width: 10px;
      height: 10px;
      margin: 5px;
      border-radius: 50%;
      background: lighten($black, 90%);
      position: relative;
      &:after {
        position: absolute;
        top: -2px;
        left: -2px;
        right: 0;
        bottom: 0;
        width: 14px;
        height: 14px;
        content: "";
        border: 1px solid rgba(255, 255, 255, 0.3);
        @include border-radius(50%);
      }
      &:hover,
      &:focus {
        outline: none !important;
      }
      &.active {
        background: lighten($black, 70%);
      }
    }
  }
  &.home-slider {
    position: relative;
    // height: 630px;
    z-index: 0;

    @include media-breakpoint-up(lg) {
      height: 750px;
    }
    .slider-item {
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      position: relative;
      z-index: 0;

      @include media-breakpoint-up(md) {
        height: 750px;
      }
      @include media-breakpoint-down(lg) {
        background-position: center center !important;
      }
      .overlay {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: $black;
        opacity: 0;
      }
      .slider-text {
        height: 750px;
        z-index: 0;
        @include media-breakpoint-down(lg) {
          height: auto;
          text-align: center;
        }
        .one-third {
          width: 60%;
          position: relative;
          z-index: -1;
          .overlay {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: $black;
            opacity: 0;
          }

          @include media-breakpoint-down(lg) {
            width: 100%;
            opacity: 0.5;
            z-index: 0;

            .overlay {
              opacity: 0.3;
            }
            &:after {
              opacity: 0;
            }
          }
        }
        .one-forth {
          width: 50%;
          position: relative;
          @include media-breakpoint-up(md) {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            width: 1200px;
            margin: 0 auto;
          }
          @media (max-width: 991.98px) {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            padding: 1em;
          }
          .text {
            width: 100%;
            @include media-breakpoint-up(lg) {
              position: absolute;
              top: 50%;
              left: 0;
              transform: translateY(-50%);
              right: 0;
              // width: 1200px;
              max-width: 50%;
              padding-left: 45px;
              padding-right: 3em;
            }
          }
        }
        .subheading {
          color: $black;
          font-weight: 600;
          font-size: 14px;
          letter-spacing: 3px;
          text-transform: uppercase;
          color: $primary;
        }
        .text {
          position: relative;
          z-index: 1;
        }
        h1 {
          font-size: 60px;
          color: $white;
          line-height: 1.2;
          font-weight: 800;
          span {
            font-weight: 800;
            color: $primary;
          }
          @media (max-width: 991.98px) {
            color: $white;
            font-size: 50px;
          }
          @include media-breakpoint-down(sm) {
            font-size: 40px;
            // color: $pri;
          }
        }
        h2 {
          font-size: 30px;
          font-weight: 400;
          @media (max-width: 991.98px) {
            color: $white;
          }
        }
        p {
          color: rgba(0, 0, 0, 0.8);
          font-weight: 400;
          @media (max-width: 991.98px) {
            color: rgba(255, 255, 255, 0.7);
            font-size: 23px;
          }
        }
        .btn-custom {
          border: 1px solid $primary;
          padding: 5px 20px;
          text-transform: uppercase;
          font-size: 12px;
          letter-spacing: 3px;
        }
      }
    }
    .owl-nav {
      position: absolute;
      bottom: 60px !important;
      left: 0;
      right: 0;
      // max-width: 1200px;
      margin: 0 auto;
      @media (max-width: 991.98px) {
        display: none;
      }
      .owl-prev,
      .owl-next {
        position: absolute;
        width: 60px;
        height: 60px;
        // color: $primary !important;
        background: $white !important;
        @include transition(0.7s);
        span {
          &:before {
            font-size: 20px;
            margin-top: 7px;
            color: $black;
          }
        }
        opacity: 1;
      }
      .owl-prev {
        top: 0 !important;
        right: 0 !important;
        @include media-breakpoint-up(lg) {
          right: 61px !important;
        }
        &:hover,
        &:focus {
          background: $black !important;
          outline: none !important;
          span {
            &:before {
              font-size: 20px;
              margin-top: 7px;
              color: $primary;
            }
          }
        }
      }
      .owl-next {
        top: 0 !important;
        right: 0 !important;
        @include media-breakpoint-up(lg) {
          right: 0 !important;
        }
        &:hover,
        &:focus {
          background: $black !important;
          outline: none !important;
          span {
            &:before {
              font-size: 24px;
              margin-top: 7px;
              color: $primary;
            }
          }
        }
      }
    }
    .owl-dots {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 40px;
      width: 100%;
      @include media-breakpoint-down(sm) {
        bottom: 5px;
      }
      .owl-dot {
        width: 10px;
        height: 10px;
        margin: 5px;
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.4);
        &.active {
          background: $white;
        }
      }
    }
    .img-circle {
      height: 500px;
      object-fit: contain;
    }
  }
}

