}

.resume-wrap {
  & {
    gap: 15px;
  }
  //   width: 100%;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  @include border-radius(5px);
  .date {
    font-weight: 900;
