//SKILLS--------------------
.progress-wrap {
  width: 100%;
  margin-bottom: 30px;
  h3 {
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: 500;
  }
}
.progress-infos {
  display: flex;
  align-items: baseline;

  span {
    margin-left: 20px;
    font-size: 20px;
    font-weight: 400;
    color: $white;
  }
  @include media-breakpoint-up(md) {
    span {
      display: none;
    }
  }
}
//PROGRESS BAR
.progress {
  height: 10px;
  box-shadow: none;
  background: lighten($black, 10%);
  overflow: visible;
}
.progress-bar {
  background: $primary;
  box-shadow: none;
  font-size: 12px;
  line-height: 1.2;
  color: $black;
  font-weight: 600;
  position: relative;
  overflow: visible;
  @include border-radius(2px);
  &:after {
    position: absolute;
    top: -2px;
    right: 0;
    width: 34px;
    height: 34px;
    content: "";
    background: $primary;
    @include border-radius(2px);
    opacity: 0;
  }
  span {
    display: none;
    position: absolute;
    top: -38px;
    right: -15px;
    font-size: 20px;
    font-weight: 400;
    color: $white;

    @include media-breakpoint-up(md) {
      display: block;
    }
  }
}

.margin-top {
  @include media-breakpoint-up(lg) {
    margin-top: -70px;
    padding-top: 10em !important;
  }
}

//PROJECT
.project {
  width: 100%;
  height: 285px;
  margin-bottom: 30px;
  position: relative;
  z-index: 0;
  &.img-2 {
    height: 600px;
    @include media-breakpoint-down(sm) {
      height: 285px;
    }
    .text {
      max-width: 45%;
      z-index: 1;
      h3 {
        font-size: 22px;
        a {
          color: $white;
        }
      }
      span {
        color: rgba(255, 255, 255, 0.8);
        text-transform: uppercase;
        letter-spacing: 2px;
      }
      @include media-breakpoint-down(sm) {
        max-width: 100%;
        opacity: 1;
        span {
          color: rgba(255, 255, 255, 0.8);
        }
      }
    }
  }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: "";
    opacity: 0;
    background: $primary;
    z-index: -1;
    @include transition(0.3s);
    @include media-breakpoint-down(sm) {
      opacity: 0.2;
    }
  }
  .text {
    max-width: 80%;
    z-index: 1;
    opacity: 0;
    @include transition(0.3s);
    h3 {
      font-size: 20px;
      a {
        color: $white;
      }
    }
    span {
      color: rgba(255, 255, 255, 0.8);
      text-transform: uppercase;
      letter-spacing: 2px;
      font-size: 12px;
      font-weight: 600;
    }
    @include media-breakpoint-down(sm) {
      opacity: 1;
      span {
        color: rgba(255, 255, 255, 0.8);
      }
    }
  }
  &:hover,
  &:focus {
    .overlay {
      opacity: 0.9;
    }
    .text {
      opacity: 1;
    }
  }
}

// USEFUL CODE
.aside-stretch {
  background: lighten($primary, 10%);
  &:after {
    position: absolute;
    top: 0;
    right: 100%;
    bottom: 0;
    content: "";
    width: 360%;
    background: lighten($primary, 10%);
    // background: #333644;
  }
  @include media-breakpoint-down(sm) {
    background: transparent;
    &:after {
      background: transparent;
