.scroll-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: $primary;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 24px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

.scroll-btn:hover {
  background-color: #fff;
  color: $primary;
}

.scroll-btn:active {
  transform: scale(0.95);
}

.scroll-btn:focus {
  outline: none;
}
